import Vector from "./vector"
export default class Point {
    #x = 0; #y = 0; #z = 0;
    constructor(x, y, z) {
        this.set(x, y, z)
    }
    clone = () => {
        return new Point(this.#x, this.#y, this.#z);
    }
    getX = () => { return this.#x }
    getY = () => { return this.#y }
    getZ = () => { return this.#z }
    translate = (vector) => {
        if (!(vector instanceof Vector)) throw new Error("Illegal argument(s) error")
        this.#x += vector.getX();
        this.#y += vector.getY();
        this.#z += vector.getZ();
        return this
    }
    setX = (x) => {
        if (this.#isValid(x)) this.#x = x; else throw new Error("Illegal argument(s) error")
        return this
    }
    setY = (y) => {
        if(this.#isValid(y)) this.#y = y; else throw new Error("Illegal argument(s) error")
        return this
    }
    setZ = (z) => {
        if (this.#isValid(z))  this.#z = z; else throw new Error("Illegal argument(s) error")
        return this
    }
    set = (x, y, z) => {
        if(this.#isValid(x))  this.#x = x; 
        if(this.#isValid(y))  this.#y = y;
        if(this.#isValid(z))   this.#z = z;
        return this
    }
    rotate = (ref, rz, ry, rx) => {
        if (!(ref instanceof Point)) throw new Error("Illegal argument(s) error")
        if (!(this.#areValid(rx,ry,rz))) throw new Error("Illegal Arguments Exception");
        this.#x -= ref.getX();
        this.#y -= ref.getY();
        this.#z -= ref.getZ();
        let v = new Vector(this.#x, this.#y, this.#z);
        v.rotate(rz, ry, rz);
        this.#x = v.getX() + ref.getX();
        this.#y = v.getY() + ref.getY();
        this.#z = v.getZ() + ref.getZ();
        return this;
    }
    from = (point) => {
        if (!(point instanceof Point)) throw new Error("Illegal argument(s) error")
        return new Vector(this.#x - point.getX(), this.#y - point.getY(), this.#z - point.getZ())
    }
    #areValid(...values) {
        for (v in values)if(!this.#isValid(v))return false;
        return true;
    }
   #isValid(v){
    if (!(typeof v === 'number') && (v > 0 || v <= 0))return false;
    return true
   }
}