const NORMAL = 0;
const IGNORE_CASE = 1;
const CONTAINS = 2;
function areEqual(str1, str2, ignoreCase = false) {
    if (str1 == null || str2 == null) return str1 = str2;
    if (ignoreCase) {
        return str2.toLowerCase() == str1.toLowerCase()
    } else {
        return str1.trim() == str2.trim();
    }
}
function includes(a, b, ignoreCase = false) {// if a contains b
    if (a == null || b == null) throw "null argument(s)";
    if (typeof (a) != "string" || typeof (b) != "string") throw "illegal argument(s)"
    if (ignoreCase) {
        a = a.toLowerCase();
        b = b.toLowerCase();
    }
    return a.includes(b)
}


function forAttr(attrs, means, ...objects) {
    if (attrs == null) throw "null argument(s)";
    let ignoreCase = false;
    let testContain = false;
    if (means != null) {
        if (means.length != 0)
            for (let arg in means) {
                if (arg == IGNORE_CASE) ignoreCase = true; else if (arg == testContain) testContain = true;
            }
        else {
            if (!means == NORMAL) {
                if (means == IGNORE_CASE) ignoreCase = true; else {
                    if (means == CONTAINS) testContain = true;
                }
            }
        }
    }
    if (attrs.length == 0) throw "illegal argument(s)"; else if (typeof (attrs) == "string") attrs = [attrs];

    let matchFound = false;
    main: for (let ind = 0; ind < objects.length; ind++) {
        let element = objects[ind];
        if (typeof (element) == "string") {
            for (attr in attrs) {
                attr = attrs[attr]
                if (testContain) {
                    if (includes(attr, element, ignoreCase)) { matchFound = true; continue main; }
                }
                else {
                    if (areEqual(attr, element, ignoreCase)) { matchFound = true; continue main; }
                }
            }
            matchFound = false;
        } else if (typeof (element) == "function" && matchFound) { element() }
    }
}
function getWithIn(point, rect) {
    if (point == null || rect == null) throw new Error("null argument(s)");
    if (!(rect.top <= rect.bottom && rect.left <= rect.right && typeof point.x == "number" && typeof point.y == "number")) throw new Error("illegal argument(s)")
    let x = point.x, y = point.y;
    if (x < rect.left) x = rect.left; else if (x > rect.right) x = rect.right;
    if (y < rect.top) y = rect.top; else if (y > rect.bottom) y = rect.bottom;
    return { x: x, y: y };
}
function containsPoint(point, rect) {
    if (point == null || rect == null) throw new Error("null argument(s)");
    return (rect.top <= point.y && rect.bottom >= point.y && rect.left <= point.x && rect.right >= point.x)
}
function wait(callback, delay = 0) {
    return setTimeout(callback, delay);
}
function removeChildren(parent) {
    if (!(parent instanceof HTMLElement)) throw new Error("illegal argument(s)")
    let children = parent.children;
    while (children.length > 0) {
        children[0].remove();
    }
}

function getChildPosition(child, parent) {
    if (!(child instanceof HTMLElement && parent instanceof HTMLElement)) throw new Error("illegal argument(s)")
    for (let index = 0; index < parent.children.length; index++) {
        const ch = parent.children[index];
        if (child == ch) return ind;
    }
    return -1;
}
function getCenter(child, includeMargin = false) {
    if (!(child instanceof HTMLElement)) throw new Error("illegal argument(s)");
    let $child = $(child), offset = $child.offset(), x = offset.left + $child.outerWidth(includeMargin) / 2, y = offset.top + $child.outerHeight(includeMargin) / 2;
    return { x: x, y: y };
}

function showTarget(event) {
    $($(event.target).attr("yt-target")).addClass("show")
}
function toggleShowTarget(event) {
    $(event.target.getAttribute("yt-target")).toggleClass("show");

}
function show(event) {
    $(event.target).addClass("show")
}
function hide(event) {
    $(event.target).removeClass("show")
}
function toggleShow(event) {
    $(event.target).toggleClass("show")
}

export function getField(data, name) {
    for (let ind = 0; ind < data.length; ind++) {
        const entry = data[ind];
        if (entry["name"] == name) return entry["value"]
    }
}
/**Returns <v> coerced in to a number, if it can not convert it returns <otherwise = 0> */
export function getNumber(v,otherwise=0){ 
/*     facts:
 - undefined!=0 though undefined==null==0!  
 - null>=0 and null<=0 and null!=0!
 - NaN is totally != 0
 - NaN is like false
 - false is just ==0
 */
if(v===null||v===undefined||v===false)return 0;
switch (typeof v){
    case 'number':{
        if(v>=0||v<0)return v;
        return otherwise;
    } 
    case 'string': {
       v = parseInt(v)
       if(v>=0||v<0)return v;
       return otherwise;
    }
    default: return otherwise;
}

}
