/**Left unchecked for NaN and undefined */
export default class Vector {
  #x = 0; #y = 0; #z = 0;
  constructor(x, y, z) {
    this.set(x, y, z)
  }
  scale = (s) => {
    if (!this.#isValid(s)) throw new Error("Illegal Arguments Exception");
    this.#x *= s; this.#y *= s; this.#z *= s;
    return this;
  }
  inverse = () => {
    this.scale(-1);
    return this;
  }
  scaleComponent = (vector, scale) => {
    vector = vector.clone()
    if (!(this.#isValid(scale) && vector instanceof Vector)) throw new Error("Illegal Arguments Exception");
    this.plus(vector.scale(this.dot(vector.setR(1))).clone().inverse());
    this.plus(vector.scale(scale));
    return this;
  }
  resetComponent = (vector, scale) => {
    let v = vector.clone()
    if (!(this.#isValid(scale) && v instanceof Vector)) throw new Error("Illegal Arguments Exception");
    let prod=this.dot(v.setR(1))
    this.plus(v.scale(prod).clone().inverse());
    this.plus(v.scale(Math.sign(prod)*scale));
    return this
  }
  setX = (x) => {
    if (!this.#isValid(x)) throw new Error("Illegal Arguments Exception");
    this.#x = x;
    return this;
  }
  setY = (y) => {
    if (!this.#isValid(y)) throw new Error("Illegal Arguments Exception");
    this.#y = y;
    return this;
  }
  serZ = (z) => {
    if (!this.#isValid(z)) throw new Error("Illegal Arguments Exception");
    this.#z = z;
    return this;
  }
  setR = (r) => {
    let s = r / this.getR();
    this.scale(s);
    return this;
  }
  normalize = () => {
    this.setR(1);
    return this
  }
  set = (x, y, z) => {
    if (this.#isValid(x)) this.#x = x;
    if (this.#isValid(y)) this.#y = y;
    if (this.#isValid(z)) this.#z = z;
    return this;
  }
  setComponent = (vector) => {
    if (!(v instanceof Vector)) throw new Error("Illegal Arguments Exception");
    let v = vector.clone();
    this.plus(v.scale(this.dot(v.setR(1))).inverse());
    this.plus(vector);
    return this;
  }
  dot = (v) => {
    return this.#x * v.getX() + this.#y * v.getY() + this.#z * v.getZ();
  }
  cross = (v) => {
    return new Vector(this.#y * v.getZ() - this.#z * v.getY(), this.#z * v.getX() - this.#x * v.getZ(), this.#x * v.getY() - this.#y * v.getX());
  }
  fitX = (x) => {
    let s = x / this.#x;
    this.scale(s)
    return this;
  }
  fitY = (y) => {
    let s = y / this.#y;
    this.scale(s)
    return this;
  }
  fitZ = (z) => {
    let s = z / this.#z;
    this.scale(s)
    return this;
  }
  fitComponent = (vector) => {
    let s = vector.getR() / this.getComponent(vector).getR();
    this.scale(s);
    return this;
  }
  rotate = (rz, ry, rx) => {
    if (this.#isValid(rz)) {
      let b = this.#x;
      this.#x = b * Math.cos(rz) - this.#y * Math.sin(rz);
      this.#y = this.#y * Math.cos(rz) + b * Math.sin(rz);
    }
    if (this.#isValid(ry)) {
      let b = this.#z;
      this.#z = b * Math.cos(ry) - this.#x * Math.sin(ry);
      this.#x = this.#x * Math.cos(ry) + b * Math.sin(ry);
    }
    if (this.#isValid(rx)) {
      let b = this.#y;
      this.#y = b * Math.cos(rx) - this.#z * Math.sin(rx);
      this.#z = this.#z * Math.cos(rx) + b * Math.sin(rx);
    }
    return this;
  }

  getX = () => { return this.#x }
  getY = () => { return this.#y }
  getZ = () => { return this.#z }
  getR = () => {
    return Math.sqrt(this.#x ** 2 + this.#y ** 2 + this.#z ** 2)
  }
  getXY = () => {
    let a = this.#x;
    let b = this.#y;
    let d = Math.acos(a / Math.sqrt(a ** 2 + b ** 2));
    d = (b >= 0) ? d : -d + 2 * Math.PI;
    return d;
  }
  getYZ = () => {
    let a = this.#y;
    let b = this.#z;
    let d = Math.acos(a / Math.sqrt(a ** 2 + b ** 2));
    d = (b >= 0) ? d : -d + 2 * Math.PI;
    return d;
  }
  getZX = () => {
    let a = this.#z;
    let b = this.#x;
    let d = Math.acos(a / Math.sqrt(a ** 2 + b ** 2));
    d = (b >= 0) ? d : -d + 2 * Math.PI;
    return d;
  }
  getComponent = (vector) => {
    if (!(v instanceof Vector)) throw new Error("Illegal Arguments Exception");
    let v = vector.clone();
    return v.scale(this.dot(v.setR(1)));
  }
  clone = () => {
    return new Vector(this.#x, this.#y, this.#z)
  }
  plus = (vector) => {
    if (!(vector instanceof Vector)) throw new Error("Illegal Arguments Exception");
    this.#x += vector.getX(); this.#y += vector.getY(); this.#z += vector.getZ();
    return this;
  }
  #isValid(val) {
    return (typeof val === 'number') && (val > 0 || val <= 0);
  }
}