import Point from "./point";
export default class LineSegment {
    constructor(a, b) {
        this.set(a, b)
    }
    set(a, b) {
        if (!(a instanceof Point && b instanceof Point)) {
            throw new Error("Illegal argument(s) error")
        }
        this.a = a; this.b = b;
        return this
    }
    length() {
        return this.a.from(this.b).getR()
    }
    getA() {
        return this.a;
    }
    getB() {
        return this.b;
    }
    get() {
        return [this.a, this.b]
    }
    to(p) {
        if (!(p instanceof Point)) {
            throw new Error("Illegal argument(s) error")
        }
        let ap = p.from(this.a)
        let ab = this.b.from(this.a)
        let aq = ab.clone();
        let q = this.a.clone().translate(aq.scale(aq.normalize().dot(ap)));
        aq = q.from(this.a)
        let bq = q.from(this.b)
        let qp = p.from(q)
        let maq = aq.getR(), mbq = bq.getR(), mpq = qp.getR()
        if (maq + mbq <= ab.getR() + 1e-14) {
            return qp;
        } else if (maq <= mbq) {
            return ap
        } else {
            return p.from(this.b)
        }
    }

    rotate(c, rz, ry, rx) {
        this.a.rotate(c, rz, ry, rx);
        this.b.rotate(c, rz, ry, rx);
        return this
    }
    translate(v) {
        this.a.translate(v);
        this.b.translate(v);
        return this
    }
    clone() {
        return new LineSegment(a, b)
    }
}