import $, { param } from "jquery";
import anime from "animejs"
import { getNumber } from "./custom";


/**Evaluates the proper positioning coordinates for the element according to the provided reference, bounds and perspective. 
 * @params el - the element to position
 * @params params - specifications: left_of, withX, midX, right_of, top_of, withY, centerX, bottom_of, x & y(overrides any other relative specification), offsetX,offsetY, offset, perspectiveX, perspectiveY, bound(Rect).
*/
export function getCoordinates(el, params) {//todo: choose the best
    let rect = el.getBoundingClientRect();

    let x = compute(rect.left, rect.width, { before: params.left_of?.getBoundingClientRect().left, after: params.right_of?.getBoundingClientRect().right, mid: params.midX?.getBoundingClientRect().left + params.midX?.getBoundingClientRect().width / 2, with: params.withX?.getBoundingClientRect().left(), value: getNumber(params.x), offset: getNumber(params.offsetX) + getNumber(params.offset), boundStart: params.bound?.left, boundEnd: params.bound?.right, perspective: getNumber(params.perspectiveX) })

    let y = compute(rect.top, rect.height, { before: params.top_of?.getBoundingClientRect().top, after: params.bottom_of?.getBoundingClientRect().bottom, mid: params.midY?.getBoundingClientRect().top + params.midY?.getBoundingClientRect().height / 2, with: params.withY?.getBoundingClientRect().top(), value: getNumber(params.y), offset: getNumber(params.offsetY) + getNumber(params.offset), boundStart: params.bound?.top, boundEnd: params.bound?.bottom, perspective: getNumber(params.perspectiveY) })

    return { x: x, y: y }
}
export function getCoordinatesFromParameters(params) {//todo: choose the best

    let x = compute(getNumber(params.left), getNumber(params.width), { before: params.left_of?.getBoundingClientRect().left, after: params.right_of?.getBoundingClientRect().right, mid: params.midX?.getBoundingClientRect().left + params.midX?.getBoundingClientRect().width / 2, with: params.withX?.getBoundingClientRect().left(), value: getNumber(params.x), offset: getNumber(params.offsetX) + getNumber(params.offset), boundStart: params.bound?.left, boundEnd: params.bound?.right, perspective: getNumber(params.perspectiveX) })

    let y = compute(getNumber(params.top), getNumber(params.height), { before: params.top_of?.getBoundingClientRect().top, after: params.bottom_of?.getBoundingClientRect().bottom, mid: params.midY?.getBoundingClientRect().top + params.midY?.getBoundingClientRect().height / 2, with: params.withY?.getBoundingClientRect().top(), value: getNumber(params.y), offset: getNumber(params.offsetY) + getNumber(params.offset), boundStart: params.bound?.top, boundEnd: params.bound?.bottom, perspective: getNumber(params.perspectiveY) })

    return { x: x, y: y }
}


let compute = (start, dimen, params) => {
    let pos;

    if (params.value !== undefined) {
        pos = params.value - (params.perspective ? params.perspective : 0);
    }
    else if (params.before !== undefined) {
        pos = params.before - dimen;
    }
    else if (params.with !== undefined) {
        pos = params.with;
    }
    else if (params.center !== undefined) {
        pos = (params.before + params.after) / 2 - dimen / 2
    }
    else if (params.after !== undefined) {
        pos = params.after;
    }
    else {
        pos = start;
    }

    if (params.offset) pos += params.offset;

    if (params.boundStart !== undefined || params.boundEnd !== undefined) {
        if (pos + dimen > params.boundEnd) pos = params.boundEnd - dimen;
        if (pos < params.boundStart) pos = params.boundStart;
    }

    if (params.perspective !== undefined) pos += params.perspective;

    return pos;
}


